                type: 'bar',
                stack: '总量',
                label: {
                  normal: {
                    show: true
                  }
                },
                data: lineValueList
              },
              {
                name: '停电',
                type: 'bar',
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    formatter: function (value) {
                      if (value.data < 0) {
                        return -value.data
                      }
                    }
                  }
                },
                data: powerValueList
              }
            ]
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    draw_dateProcesstime () {
      this.$http({
        method: 'post',
        url: 'http://132.96.194.30:8078/report/specific/',
        data: {
          'statisticsType': 5,
          'begin_datetime': this.myBegin_datetime,
          'end_datetime': this.myEnd_datetime
        }
      }).then(response => {
        if (response.data.status) {
          var cityList = []
          var lineTimeValueList = []
          var powerTimeValueList = []
          var data = response.data.result
          var l = response.data.result.length
          for (var i = 0; i < l; i++) {
            cityList.push(data[i]['city'])
            lineTimeValueList.push(data[i]['line_time'])
            powerTimeValueList.push(data[i]['power_time'] * (-1))
          }
          let myChart = this.$echarts.init(document.getElementById('chart_dateProcesstime'))
          myChart.setOption({
            title: {
              text: '线路及停电故障处理时长'
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: { // 坐标轴指示器，坐标轴触发有效
                type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
              }
            },
            legend: {
              data: ['线路故障处理时长（小时）', '停电故障处理时长（小时）']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
            xAxis: [
              {
                type: 'value',
                axisLabel: {
                  formatter: function (value) {
                    return Math.abs(value) // 显示的数值都取绝对值
                  }
                }
              }
            ],
            yAxis: [
              {
                type: 'category',
                axisTick: {show: false},
                data: cityList
              }
            ],
            series: [
              {
                name: '线路',
                type: 'bar',
                stack: '总量',
                label: {
                  normal: {
                    show: true
                  }
                },
                data: lineTimeValueList
              },
              {
                name: '停电',
                type: 'bar',
                stack: '总量',
                label: {
                  normal: {
                    show: true,
                    formatter: function (value) {
                      if (value.data < 0) {
                        return -value.data
                      }
                    }
                  }
                },
                data: powerTimeValueList
              }
            ]
          })
        }
      }).catch(error => {
        console.log(error)
      })
    },
    DataFilter () {
      this.table_dateCount()
      this.draw_dateCount()
      this.table_dateQuality()
      this.draw_dateIntimeRate()
      this.draw_dateAverageTime()
      this.draw_dateOver48Rate()
      this.table_dateAnalysis()
      this.table_dateProcesstime()
      this.draw_dateAnalysis()
      this.draw_dateLinePower()
      this.draw_dateProcesstime()
    }
  },
  mounted () {

  }
}
</script>
<style>
  .el-header{
        height: 180px;
        color: rgb(110,110,110);
        text-align: center;
        line-height: 60px;
        font: 28px/60px bold 微软雅黑;
    }
  .el-main {
        /*background-color: #E9EEF3;*/
        color: #333;
    }
  .main {
      margin: 0 auto;
      width: 900px;
    }
  .main h3{
      font: 18px/20px bold 微软雅黑;
      color: rgb(130,130,130);
      text-align:left;
    }
  .table_dateCount,.table_dateQuality,.table_dateAnalysis,.table_dateProcesstime{
    margin: 20px 90px;
  }
  .el-table .cell{
    line-height: 15px;
  }
  .el-form-item{
    margin-bottom: -13px;
  }
  .el-form-item__content{
    margin-left: -100px;
    margin-right: 20px;
  }

</style>



